@charset "UTF-8";
@font-face {
  font-family: "Poppins";
  src: url("./../fonts/Poppins-Regular.ttf");
}
@font-face {
  font-family: "Poppins";
  src: url("./../fonts/Poppins-Bold.ttf");
  font-weight: bold;
}
@media (max-width: 767px) {
  .custom-toggler .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  }
}

@media (max-width: 767px) {
  .custom-toggler.navbar-toggler {
    border-color: white;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.alignwide {
  margin: 0 auto;
  max-width: 1300px;
}

.alignfull {
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
  max-width: 100vw;
}

.alignfull img {
  width: 100vw;
}

.site-layout {
  font-family: Poppins;
}
.site-layout .navbar {
  min-height: 60px;
}
@media (max-width: 767px) {
  .site-layout .navbar {
    min-height: unset;
  }
}
.site-layout .navbar .navbar-items {
  display: flex;
  width: 1400px;
  margin: 0 auto;
  align-items: center;
}
@media (max-width: 767px) {
  .site-layout .navbar .navbar-items {
    width: 100%;
    justify-content: space-around;
    margin: 10px 0;
    flex-wrap: wrap;
    align-items: center;
  }
}
@media (max-width: 767px) {
  .site-layout .navbar .navbar-items #navbarMenu {
    margin-bottom: -18px;
    margin-top: 10px;
  }
}
.site-layout .navbar .navbar-items #navbarMenu ul {
  margin-top: 1rem;
}
.site-layout .navbar .navbar-items #navbarMenu ul li a {
  color: #404040;
  font-size: 18px;
  transition: 0.2s ease;
  position: relative;
  padding-bottom: 5px;
}
.site-layout .navbar .navbar-items #navbarMenu ul li a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #18272f;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
}
.site-layout .navbar .navbar-items #navbarMenu ul li a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}
@media (max-width: 767px) {
  .site-layout .navbar .navbar-items #navbarMenu ul li a {
    font-size: 22px;
  }
}

.button-rounded-orange {
  text-decoration: none;
  color: white;
  background-color: rgb(255, 93, 6);
  padding: 10px 35px;
  border-radius: 20px;
  font-size: 22px;
  transition: 0.3s;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.button-rounded-orange:hover {
  background-color: #c24400;
  color: white;
}

.single-post__wrapper__content {
  flex-direction: column;
}
.single-post__wrapper__content .single-post__gallery {
  display: flex;
  gap: 27px;
  margin: 0 10%;
  margin-top: 30px;
  margin-bottom: 100px;
}
.single-post__wrapper__content .single-post__gallery img {
  flex-basis: 33%;
  width: 100%;
}

@media (min-width: 980px) {
  .site-container {
    width: 1300px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .site-container {
    width: 100% !important;
  }
}

.site-layout {
  font-family: Poppins;
  overflow-x: hidden;
}
.site-layout .title-01 {
  text-align: center;
  font-size: 45px;
}
.site-layout .navbar {
  background-color: black !important;
}
.site-layout .navbar .navbar-items #navbarMenu ul li {
  margin-bottom: 10px;
}
.site-layout .navbar .navbar-items #navbarMenu ul li a {
  transition: 0.3s ease;
  color: white;
}
.site-layout .navbar .navbar-items #navbarMenu ul li a::before {
  background-color: #ff5c06;
}
.site-layout .navbar .navbar-items #navbarMenu ul li a:hover {
  color: #ff5c06;
}
.site-layout #start__slider {
  position: relative;
}
.site-layout #start__slider .start__slider__content {
  position: absolute;
  top: 20%;
  z-index: 3;
  padding: 30px;
  border-radius: 20px;
  text-align: center;
  width: 80%;
  left: 10%;
}
@media (max-width: 767px) {
  .site-layout #start__slider .start__slider__content {
    width: 60%;
    left: 20%;
    padding: 0;
  }
}
.site-layout #start__slider .start__slider__content h1 {
  color: #ff5d06;
  font-weight: bold;
  font-size: 100px;
  text-shadow: 0px 4px 3px rgba(0, 0, 0, 0.4), 0px 8px 13px rgba(0, 0, 0, 0.1), 0px 18px 23px rgba(0, 0, 0, 0.1);
}
@media (max-width: 767px) {
  .site-layout #start__slider .start__slider__content h1 {
    font-size: 34px;
  }
}
.site-layout #start__slider .start__slider__content h2 {
  color: white;
  margin-bottom: 30px;
  text-shadow: 0px 4px 3px rgba(0, 0, 0, 0.5), 0px 8px 13px rgba(0, 0, 0, 0.3), 0px 18px 23px rgba(0, 0, 0, 0.3);
  font-size: 45px;
  margin-top: 20px;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .site-layout #start__slider .start__slider__content h2 {
    font-size: 22px;
    margin-bottom: 60px;
    margin-top: 50px;
  }
}
.site-layout #start__slider .start__slider__content a {
  text-decoration: none;
  color: white;
  background: #ff5d06;
  padding: 10px 35px;
  border-radius: 20px;
  font-size: 22px;
  transition: 0.3s;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
@media (max-width: 767px) {
  .site-layout #start__slider .start__slider__content a {
    font-size: 16px;
  }
}
.site-layout #start__slider .start__slider__content a:hover {
  background-color: #c24400;
}
.site-layout #start__slider .ub_image_slider {
  height: calc(100vh - 95px);
}
.site-layout #start__slider .ub_image_slider .swiper-wrapper figure img {
  height: calc(100vh - 95px);
  width: 100%;
  object-fit: cover;
  object-position: bottom;
  max-height: unset;
}
.site-layout #start__slider .ub_image_slider .swiper-button-prev {
  color: #ff5c06;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.2901960784);
  padding: 33px !important;
  border-radius: 50%;
  transition: 0.4s ease;
}
@media (max-width: 767px) {
  .site-layout #start__slider .ub_image_slider .swiper-button-prev {
    padding: 24px !important;
  }
}
.site-layout #start__slider .ub_image_slider .swiper-button-prev::after {
  font-size: 28px;
  margin-left: -5px;
}
.site-layout #start__slider .ub_image_slider .swiper-button-prev:hover {
  background-color: white;
}
.site-layout #start__slider .ub_image_slider .swiper-button-next {
  color: #ff5c06;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.2901960784);
  padding: 33px !important;
  border-radius: 50%;
  transition: 0.4s ease;
}
@media (max-width: 767px) {
  .site-layout #start__slider .ub_image_slider .swiper-button-next {
    padding: 24px !important;
  }
}
.site-layout #start__slider .ub_image_slider .swiper-button-next::after {
  font-size: 28px;
  margin-left: 5px;
}
.site-layout #start__slider .ub_image_slider .swiper-button-next:hover {
  background-color: white;
}
.site-layout #start__slider .ub_image_slider .swiper-pagination-bullet {
  width: 13px;
  height: 13px;
}
.site-layout #start__slider .ub_image_slider .swiper-pagination-bullet-active {
  background-color: #ff5c06;
}
.site-layout footer {
  background-color: black;
}
.site-layout footer .footer__wrapper {
  color: white;
  width: 100%;
}
.site-layout footer .footer__wrapper .footer__wrapper__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
}
.site-layout footer .footer__wrapper .footer__wrapper__container .footer-logo {
  max-width: 120px;
  margin-bottom: 50px;
}
.site-layout footer .footer__wrapper .footer__wrapper__container p a {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: white;
  transition: 0.2s;
}
.site-layout footer .footer__wrapper .footer__wrapper__container p a:hover {
  color: #ff5c06;
}
.site-layout footer .footer__wrapper .footer__wrapper__container p a img {
  width: 27px;
  margin-right: 10px;
}
.site-layout footer .footer__wrapper .footer__wrapper__container .footer_wrapper__container__pages {
  width: 100%;
}
.site-layout footer .footer__wrapper .footer__wrapper__container .footer_wrapper__container__pages nav {
  display: flex;
  gap: 50px;
  border-bottom: 1px solid #fb5c05;
  justify-content: center;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  .site-layout footer .footer__wrapper .footer__wrapper__container .footer_wrapper__container__pages nav {
    flex-direction: column;
    gap: 0;
    padding-bottom: 30px;
  }
}
.site-layout footer .footer__wrapper .footer__wrapper__container .footer_wrapper__container__pages nav a {
  transition: 0.2s;
}
.site-layout footer .footer__wrapper .footer__wrapper__container .footer_wrapper__container__pages nav a:hover {
  color: #ff5c06 !important;
}
.site-layout footer .footer__wrapper .footer__wrapper__container__cta {
  display: flex;
  gap: 50px;
  margin-bottom: 10px;
}
.site-layout footer .footer__wrapper .footer_wrapper__copyright {
  text-align: center;
  padding-bottom: 15px;
  font-size: 14px;
  color: #ddd;
}
.site-layout #start__uber-uns {
  align-items: center !important;
}
@media (max-width: 767px) {
  .site-layout #start__uber-uns {
    padding: 0 20px;
  }
}
.site-layout #start__uber-uns figure {
  position: relative;
}
.site-layout #start__uber-uns figure::before {
  position: absolute;
  top: 0;
  left: -75%;
  z-index: 2;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  -webkit-transform: skewX(-25deg);
  transform: skewX(-25deg);
}
.site-layout #start__uber-uns figure:hover::before {
  -webkit-animation: shine 0.75s;
  animation: shine 0.75s;
}
@-webkit-keyframes shine {
  100% {
    left: 125%;
  }
}
@keyframes shine {
  100% {
    left: 125%;
  }
}
.site-layout #start__uber-uns figure img {
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.site-layout #start__uber-uns .start__uber-uns__text h3 {
  font-size: 33px;
  text-align: center;
}
.site-layout #start__uber-uns .start__uber-uns__text p {
  text-align: center;
  margin-top: 50px;
}
.site-layout #start__uber-uns .start__uber-uns__text p a {
  font-size: 17px;
}
.site-layout #start__realizations {
  background-color: #f7f7f7;
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .site-layout #start__realizations {
    padding: 0 20px;
  }
}
.site-layout .start__realizations__container {
  display: flex;
  gap: 30px;
}
@media (max-width: 767px) {
  .site-layout .start__realizations__container {
    flex-direction: column;
  }
}
.site-layout .start__realizations__container .start__realizations__containe__box {
  flex-basis: 33%;
  background-color: black;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  overflow: hidden;
  z-index: 1;
}
.site-layout .start__realizations__container .start__realizations__containe__box:hover img {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.site-layout .start__realizations__container .start__realizations__containe__box img {
  width: 100%;
  object-fit: cover;
  object-position: center;
  max-height: 300px;
  border-radius: 10px 10px 0 0;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.site-layout .start__realizations__container .start__realizations__containe__box a {
  display: block;
  overflow: hidden;
}
.site-layout .start__realizations__container .start__realizations__containe__box p {
  text-align: center;
  z-index: 2;
  margin: 0;
}
.site-layout .start__realizations__container .start__realizations__containe__box p a {
  color: #ff5d06;
  text-decoration: none;
  font-size: 22px;
  padding: 22px 0;
}
.site-layout .start__realizations-more {
  text-align: center;
  margin-top: 50px;
}
.site-layout .start__realizations-more a {
  font-size: 17px;
}

